@import "variables";

$fonts : (
  (family : "Poppins", weight : 700, style : normal, filename : "Poppins"),
  (family : "Poppins", weight : 500, style : normal, filename : "Poppins"),
  (family : "Poppins", weight : 300, style : normal, filename : "Poppins"),
  (family : "Poppins", weight : 400, style : normal, filename : "Poppins"),
);

@each $font in ($fonts) {
  @font-face {
    font-family : map-get($font, family);
    font-weight : map-get($font, weight);
    font-style : map-get($font, style);
    src : 
          local('#{map-get($font, family)}-Regular'),
          url('../webroot/fonts/#{map-get($font, filename)}-400.woff') format("woff"),
          url('../webroot/fonts/#{map-get($font, filename)}-300.woff') format("woff"),
          url('../webroot/fonts/#{map-get($font, filename)}-500.woff') format("woff"),
          url('../webroot/fonts/#{map-get($font, filename)}-Black.woff') format("woff"),
          url('../webroot/fonts/#{map-get($font, filename)}-400.woff2') format("woff2"),
          url('../webroot/fonts/#{map-get($font, filename)}-300.woff2') format("woff2"),
          url('../webroot/fonts/#{map-get($font, filename)}-500.woff2') format("woff2"),
          url('../webroot/fonts/#{map-get($font, filename)}-Black.woff2') format("woff2"),
  }
}

